import React from "react";
import { Box, Button, Center, Flex, Image, Text } from "@chakra-ui/react";
import { Icon } from "@iconify/react";
const Header = () => {
  return (
    <Flex className="header">
      <Image src="logo.png" width="100px" ml="15px" />
      <Flex my="auto" ml="auto">
        <Center bg="white" w="30px" h="30px" borderRadius="50%" mr="15px">
          <Icon icon="ic:sharp-facebook" style={{ fontSize: "20px" }} />
        </Center>
        <Center bg="white" w="30px" h="30px" borderRadius="50%" mr="15px">
          <Icon icon="mingcute:whatsapp-fill" style={{ fontSize: "20px" }} />
        </Center>
        <Center mr="15px" w="30px" h="30px">
          <Icon
            icon="mdi:phone-message-outline"
            style={{ fontSize: "30px", color: "white", fontWeight: "100" }}
          />
        </Center>
        <Center mr="15px" h="30px">
          <Box color="rgba(255, 255, 255, 1)" fontSize="14px" fontWeight="400">
            <Text>call agent</Text>
            <Text>0340 1234567</Text>
          </Box>
        </Center>
      </Flex>
    </Flex>
  );
};

export default Header;
