import React from "react";
import { Box, Button, Text } from "@chakra-ui/react";
const MainFeatures = ({ heading, setLineShow }) => {
  return (
    <Box width="100%">
      <Text
        color="white"
        fontWeight="700"
        fontSize={{ "2xl": "70px", xl: "46px", lg: "38px", sm: "40px" }}
        textAlign={heading == "Products" ? "left" : "right"}
        width="100%"
      >
        {heading}
      </Text>
      <div
        style={{
          color: "white",
          fontWeight: "350",
          fontSize: "20px",
          width: "100%",
          textAlign: heading == "Products" ? "left" : "right",
        }}
      >
        {/* <span style={{ textAlign: "right" }}>
          I focused on revamping specific
        </span> */}
        <Text>
          I focused on revamping specific metrics on the website, addressing the
          identified issues in a targeted manner.
        </Text>
      </div>
      <Button
        color="white"
        fontWeight="400"
        fontSize="20px"
        background="transparent"
        borderRadius="20px"
        border="1px solid white"
        paddingTop="10px"
        paddingBottom="10px"
        paddingLeft="25px"
        paddingRight="25px"
        marginTop="2%"
        display="flex"
        marginLeft={heading == "Products" ? "" : "auto"}
        onClick={() => setLineShow(true)}
      >
        Get in touch
      </Button>
    </Box>
  );
};

export default MainFeatures;
