import React, { useEffect, useRef, useState } from "react";
import MainFeatures from "../components/HomePage/MainFeatures";
import { useDimensions } from "@chakra-ui/react";
import Fade from "react-reveal/Fade";
import Pulse from "react-reveal/Pulse";
import { Icon } from "@iconify/react";
import Consultancy from "./Consultancy";
const HomePage = ({ setPNum, windowSize, setWindowSize }) => {
  const [lineShow, setLineShow] = useState(false);
  return (
    <div
      style={{
        height: "90%",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      {windowSize[0] <= 770 ? null : (
        <div className={`container ${lineShow ? "right" : ""}`}>
          <button
            onClick={() => setLineShow(!lineShow)}
            className="arrow-button"
          >
            <Icon
              icon="ooui:next-rtl"
              // style={{ fontSize: "30px", color: "white", fontWeight: "100" }}
            />
          </button>
          <div className={`line ${lineShow ? "right" : ""}`}></div>
          <button
            onClick={() => setLineShow(!lineShow)}
            className="arrow-button"
            style={{ marginTop: "50px" }}
          >
            <Icon
              icon="ooui:next-ltr"
              // style={{ fontSize: "30px", color: "white", fontWeight: "100" }}
            />
          </button>
        </div>
      )}
      {lineShow ? (
        <div style={{ width: "90%" }}>
          <Fade left>
            <Consultancy windowSize={windowSize} />
          </Fade>
        </div>
      ) : (
        <div
          style={{
            display: "flex",
            justifyContent: "center",
            flexDirection: windowSize[0] <= 770 ? "column" : "row",
            alignItems: "center",
          }}
        >
          <div
            style={{
              width: windowSize[0] <= 770 ? "100%" : "35%",
              padding: "4%",
            }}
          >
            <Pulse>
              <MainFeatures heading="Consultancy" setLineShow={setLineShow} />
            </Pulse>
          </div>
          <div
            style={{
              width: windowSize[0] <= 770 ? "100%" : "35%",
              padding: "4%",
            }}
          >
            <Pulse>
              <MainFeatures heading="Products" />
            </Pulse>
          </div>
        </div>
      )}
    </div>
  );
};

export default HomePage;
